import { FC } from 'react';

import { Box, Container, generateUtilityClasses, Grid } from '@mui/material';
import { zodNavigatorSchemas } from '@packages/providers';
import { useQueryClient } from '@tanstack/react-query';
import { getNavigatorItems } from 'helpers/getNavigatorItems';
import { getNavigatorPathnames } from 'helpers/getNavigatorPathnames';
import { prefetchPage, useGetWebsite, useTranslate } from 'hooks/index';
import get from 'lodash/get';
import { FOOTER_HEIGHT, styled } from 'theme/index';

import { Link } from '../../links/Link';
import { Typography } from '../../Typography';

const PREFIX = 'Footer';

export const FooterClasses = generateUtilityClasses(PREFIX, ['root']);

const StyledFooter = styled('div', {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => ({
  [`& .${FooterClasses.root}`]: {
    backgroundColor: theme.palette.background.default,
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(3, 5),
    [theme.breakpoints.down('md')]: {
      padding: theme.spacing(3, 5, 2, 5),
    },
  },
}));

export const Footer: FC = () => {
  const queryClient = useQueryClient();

  const { data: websiteData } = useGetWebsite();

  const translate = useTranslate({ basePath: 'footer' });

  const navigators = get(websiteData, 'navigators') || [];
  const companyName = get(websiteData, 'company.name');

  const footerItems = getNavigatorItems({
    navigatorsData: zodNavigatorSchemas.index.parse(navigators),
    selector: 'footer',
  });

  return (
    <StyledFooter>
      <Container>
        <Box className={FooterClasses.root} minHeight={FOOTER_HEIGHT}>
          <Grid container gap={{ xs: 3, md: 0 }} alignItems="center">
            <Grid
              item
              md={4}
              container
              justifyContent={{ xs: 'center', md: 'start' }}
              sx={{ order: { xs: 1, md: 0 } }}
            >
              <Typography variant="body1" component="span" color="text.light" dir="ltr">
                {translate('year', { year: new Date().getFullYear(), company: companyName })}
              </Typography>
            </Grid>
            <Grid
              item
              md={8}
              container
              justifyContent={{ md: 'flex-end' }}
              alignItems="center"
              flexDirection={{ xs: 'column', md: 'row' }}
              textAlign={{ xs: 'center', md: 'end' }}
              gap={{ xs: 2, md: 3 }}
              sx={{ order: { xs: 0, md: 1 } }}
            >
              {footerItems.map((navigator) => {
                const { target, title, id, path = '' } = navigator;

                const { pathname, prefetchPath, isPrefetchPath } = getNavigatorPathnames(path);

                return (
                  <Link
                    key={id}
                    t={title}
                    sx={{ textDecoration: 'none', wordBreak: 'break-word' }}
                    variant="body1"
                    href={{
                      pathname,
                    }}
                    target={target}
                    color="text.light"
                    onMouseEnter={() => {
                      if (isPrefetchPath) {
                        prefetchPage(queryClient, undefined, { path: prefetchPath });
                      }
                    }}
                  />
                );
              })}
            </Grid>
          </Grid>
        </Box>
      </Container>
    </StyledFooter>
  );
};

export default Footer;
